/* main */
body {
  background-color: #f3f3f3; }

.container {
  width: 1330px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%;
  height: 100vh; }

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  z-index: 4;
  opacity: 0;
  visibility: hidden;
  pointer-events: none; }
  .overlay.active {
    visibility: visible;
    pointer-events: auto; }

.sidebar {
  min-height: 830px;
  padding: 0 10px;
  width: 30%; }
  .sidebar-left {
    border-left: 1px solid #dadada;
    border-right: 1px solid #dadada;
    max-width: 150px; }
  .sidebar-right {
    max-width: 360px; }

.wrapper {
  display: flex;
  justify-content: space-between; }

.car {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .car-head {
    width: 100%;
    display: flex;
    justify-content: center; }
  .car-body {
    background-color: gray;
    position: relative; }
  .car-line {
    position: absolute;
    background-color: #a7a7a7; }
    .car-line--total-top, .car-line--total-bottom {
      width: 50px;
      height: 1px;
      left: -50px; }
    .car-line--total-top {
      top: 2px; }
    .car-line--total-bottom {
      bottom: 2px; }
    .car-line--total-left {
      height: calc(100% - 4px);
      width: 1px;
      top: 2px;
      left: -50px; }
    .car-line--total-width-right, .car-line--total-width-left {
      height: 50px;
      width: 1px;
      bottom: -50px; }
    .car-line--total-width-right {
      right: 1px; }
    .car-line--total-width-left {
      left: 1px; }
    .car-line--total-width-bottom {
      width: calc(100% - 2px);
      height: 1px;
      left: 1px;
      right: 1px;
      bottom: -50px; }
    .car-line--load-top, .car-line--load-bottom {
      width: 50px;
      height: 1px;
      right: -50px; }
    .car-line--load-top {
      top: 2px; }
    .car-line--load-bottom {
      top: 52px; }
    .car-line--load-right {
      height: 50px;
      top: 2px;
      width: 1px;
      right: -50px; }
  .car-count {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(-90deg);
    font-size: 16px;
    font-family: sans-serif;
    text-align: center;
    font-weight: 600; }
    .car-count--total {
      left: -50px;
      top: 45%; }
      .car-count--total-width {
        bottom: -45px;
        left: 50%;
        transform: translateX(-50%); }
    .car-count--loaded {
      right: -50px;
      top: 20px; }
  .car-reels {
    position: relative;
    background: linear-gradient(to right, #e4e3e8 25%, #d3d2d7 68%, #a4a2a5 100%);
    box-shadow: rgba(0, 0, 0, 0.25) 0 0 15px inset;
    border: 2px solid #98979c;
    padding: 5px;
    box-sizing: content-box;
    min-width: 120px;
    max-width: 120px;
    min-height: 669px;
    max-height: 669px; }
  .car-info {
    position: relative;
    width: 100%;
    min-height: 400px;
    height: auto;
    padding: 35px;
    border: 1px solid #d0d0d0; }
    .car-info__block {
      position: absolute;
      top: -1px;
      right: -1px;
      width: 30px;
      height: 30px;
      border-top: 1px solid #f3f3f3;
      border-right: 1px solid #f3f3f3;
      border-left: 1px solid #d0d0d0;
      border-bottom: 1px solid #d0d0d0;
      transition: .3s; }
    .car-info__line {
      position: absolute;
      top: -7px;
      right: 15px;
      width: 1px;
      height: 44px;
      background: #d0d0d0;
      transform: rotate(-45deg);
      transition: .3s; }
  .car-sizes {
    margin: 20px 0;
    padding: 15px 0 18px 0;
    border-top: 1px solid #d0d0d0;
    border-bottom: 1px solid #d0d0d0; }
  .car-size:not(:first-child) {
    margin-top: 35px; }
  .car-size__width, .car-size__height {
    display: flex; }

.reel {
  position: relative;
  width: 100%;
  padding-top: 100%;
  background-color: #fff;
  margin: 10px 0;
  border: 3px solid transparent;
  cursor: pointer;
  transition: border-color 0.3s; }
